
import bridge from "@vkontakte/vk-bridge";

import { updateUserStateParams } from '../../app/store/userReducer';

export const userInfoThunk = () => {
    return (dispatch) => {
		
		bridge.send(
            "VKWebAppGetUserInfo"
        ).then(data => {
            // Обработка события в случае успеха
            //console.log(data);

            dispatch(updateUserStateParams({
                vkData: data,
            }))
        })
        .catch(error => {
            // Обработка события в случае ошибки
            console.log('user data error');
            console.log(error);
        });
    }
}
