
import bridge from "@vkontakte/vk-bridge";

//import axios from 'axios';

import { parseQueryString } from '../../utils/app/parseQueryString';

import { updateAppState } from '../../app/store/appReducer';
import { updateUserStateParams } from '../../app/store/userReducer';

import { userInfoThunk } from './userInfoThunk';

export const appInitThunk = (param = null) => {

    return (dispatch, getState) => {
		
		//console.log('location', getState().app);
        
        let searchParams = parseQueryString(getState().app.locationSearch);
        let appID = parseInt(searchParams.vk_app_id) || 0;
        dispatch(updateAppState({
            appID: appID,
            searchParams: searchParams,
            //hashParams: hash,
        }));
        
        let vk_uid = parseInt(searchParams.vk_user_id) || 0;
        dispatch(updateUserStateParams({
            id: vk_uid,
        }));

        getUserToken( dispatch, appID );

        if (searchParams && searchParams.vk_platform && searchParams.vk_platform !== "desktop_web") {
            dispatch(updateAppState({
                isMobileView: true,
            }));
        }

        // Get User Info
        dispatch(userInfoThunk());

        //setResizeRoutine(searchParams, dispatch, getState);
        // TODO: надо как-то роутить все это...

    }
}

const getUserToken = ( dispatch, appID = 0, scope = '', tryNo = 1) => {

    // TODO: нужна ли вся эта система с повторами?
    if (tryNo > 10) {
        return;
    }

    bridge.send(
        "VKWebAppGetAuthToken", 
        {
            "app_id": appID, 
            "scope": scope,
        }
    ).then((data) => {
        // Обработка события в случае успеха
        //console.log('VKWebAppGetAuthToken res', data);

        dispatch(updateAppState({
            apiToken: data.access_token
        }));

        if ( (!data.scope) || (data.scope === '') ) {
            return;
        }

        let tokenByPermission = {};
        String(data.scope).split(',').forEach((permission) => {
            tokenByPermission[permission] = data.access_token;
        });
        dispatch(updateAppState({
            apiTokenByPermission: tokenByPermission
        }));

    })
    .catch((error) => {
        // Обработка события в случае ошибки
        console.log('VKWebAppGetAuthToken error', error);
        setTimeout( () => {
            getUserToken( dispatch, appID, scope, tryNo + 1);
        }, 1500 * tryNo );
    });

}

const setResizeRoutine = (searchParams, dispatch, getState) => {

    //console.log('setResizeRoutine', searchParams);

    if (searchParams && searchParams.vk_platform && searchParams.vk_platform === "desktop_web") {
        setInterval(() => {
            resizeRoutine(dispatch, getState);
        }, 5100);
    }
}

const resizeRoutine = (dispatch, getState) => {

    console.log('resizeRoutine');

    let app = getState().app;
    let currentWidth = app.currentWidth;
    let currentHeight = app.currentHeight;

    let el = document.getElementById('mainPanel');
    if (!el) {
        return false;
    }
    el = el.getElementsByClassName('Panel__in-after')[0];
    if (!el) {
        return false;
    }
    let elYBottom = el.getBoundingClientRect().bottom;

    // Condition
    if ( ( elYBottom === currentHeight ) ) {
        return;
    }

    dispatch(updateAppState({
        currentHeight: elYBottom,
    }));

    bridge.send("VKWebAppResizeWindow", {
        "width": 500,
        "height": elYBottom+50,
    }).then((data) => {
        // Обработка события в случае успеха
        console.log('VKWebAppResizeWindow res', data);
        /*
        if (data.result && data.result.height) {
            config.appWidth = data.result.width;
            config.appHeight = data.result.height;
        }
        */
    })
    .catch((error) => {
        // Обработка события в случае ошибки
        console.log('VKWebAppResizeWindow error', error);
    });
}