import React from 'react';

import bg_img from '../../css/bg.svg'

class Result extends React.Component {
	
    componentDidMount = () => {
    }
	
    componentDidUpdate = () => {
    }
	
	colorize = (clsName, arkaneValue) => {
		if ( this.props.digitsCount[arkaneValue] > 2 ) {
			return this.format(clsName + " green-text", arkaneValue);
		}
		if ( this.props.digitsCount[arkaneValue] > 1 ) {
			return this.format(clsName + " red-text", arkaneValue);
		}
		return this.format(clsName, arkaneValue);
	}

	format = (clsName, arkaneValue) => {
		if ( arkaneValue > 9 ) {
			return clsName + " point--two-numbers";
		}
		return clsName;
	}

	render = () => {

        //console.log('props', this.props);
		// red-text
		// green-text

		return (
			<div className={"result"} style={{display: "flex"}}>
				<h2 className={"h2margin"}>Ваша дата рождения<br/><span>{`${this.props.birthDayInput}.${this.props.birthMonthInput}.${this.props.birthYearInput}`}</span></h2>
					<div className={"container data"}>
						<div className={"figure"} style={{backgroundImage: `url(${bg_img})`}}>

							<span className={this.colorize("point point-1", this.props.arkanes[1])}>{this.props.arkanes[1]}</span>
							<span className={this.colorize("point point-2", this.props.arkanes[2])}>{this.props.arkanes[2]}</span>
							<span className={this.colorize("point point-3 point--right", this.props.arkanes[3])}>{this.props.arkanes[3]}</span>
							<span className={this.format("point point-4 red-text", this.props.arkanes[4])}>{this.props.arkanes[4]}</span>
							<span className={this.colorize("point point-5 point--right", this.props.arkanes[5])}>{this.props.arkanes[5]}</span>
							<span className={this.colorize("point point-6", this.props.arkanes[6])}>{this.props.arkanes[6]}</span>

							<span className={this.format("point point-7 point--right", this.props.arkanes[7])}>{this.props.arkanes[7]}</span>
							<span className={this.format("point point-8", this.props.arkanes[8])}>{this.props.arkanes[8]}</span>

							<span className={this.format("point point-9", this.props.arkanes[9])}>{this.props.arkanes[9]}</span>
							<span className={this.format("point point-10 point--right", this.props.arkanes[10])}>{this.props.arkanes[10]}</span>
							<span className={this.format("point point-11 red-text", this.props.arkanes[11])}>{this.props.arkanes[11]}</span>
							<span className={this.format("point point-12", this.props.arkanes[12])}>{this.props.arkanes[12]}</span>
							<span className={this.format("point-sum point-sum--1-2-4 blue-text", this.props.innerArkanes[1])}>{this.props.innerArkanes[1]}</span>
							<span className={this.format("point-sum point-sum--4-2-5 blue-text", this.props.innerArkanes[3])}>{this.props.innerArkanes[3]}</span>
							<span className={this.format("point-sum point-sum--2-3-5 blue-text", this.props.innerArkanes[2])}>{this.props.innerArkanes[2]}</span>
							<span className={this.format("point-sum point-sum--4-5-6 blue-text", this.props.innerArkanes[4])}>{this.props.innerArkanes[4]}</span>
							<span className={this.format("point-sum point-sum--9-11-10 blue-text", this.props.innerArkanes[0])}>{this.props.innerArkanes[0]}</span>
						</div>
						<div className={"information"}>
							<p>Возраст души:<span className={"information_value information_age-soul red-text"}>{this.props.soulAge}</span></p>
							<p>Мужские арканы:<span className={"information_value information_male-arc red-text"}>{this.props.genderArkanes.m}</span></p>
							<p>Женские арканы:<span className={"information_value information_female-arc red-text"}>{this.props.genderArkanes.f}</span></p>
							<p>Нейтральные арканы:<span className={"information_value information_light-arc red-text"}>{this.props.genderArkanes.n}</span></p>
							<p>М/Ж энергии:
								<span className={"information_value information_energy red-text"}>
									{this.props.energyArkanes.m}{ this.props.maleIsFlipped ? '-' : '' }
									/
									{this.props.energyArkanes.f}{ this.props.femaleIsFlipped ? '-' : '' }
								</span>
							</p>
							<p>Кармические арканы:<span className={"information_value information_carm-arc red-text"}>{this.props.energyArkanes.k}</span></p>
						</div>
					</div>
					<div className={"container buttons"}>
						<button className={"button button-anew"} onClick={this.props.onReset}>Рассчитать заново</button>
						<button className={"button"} onClick={this.props.onShare}>Поделиться</button>
					</div>
					<p>Хотите знать больше?</p>
					<button onClick={this.props.onDecode} className={"button button--red "}>Расшифровка</button>
    		</div>
		);
	}

}

export default Result;
