import bridge from "@vkontakte/vk-bridge";

//import { updateUserStateParams } from '../../app/store/userReducer';
import { setRouteName } from '../../app/store/routerReducer';
import { ROUTE_ARKANE_DECODE } from '../../app/router';

export const decodeThunk = () => {

    return (dispatch, getState) => {

        let state = getState();
        
        /*
        dispatch(updateUserStateParams({
            // birthDayInput: '',
            // birthMonthInput: '',
            // birthYearInput: '',
        }));
        */

        dispatch(setRouteName(ROUTE_ARKANE_DECODE));

        if (state.app.searchParams && state.app.searchParams.vk_platform && state.app.searchParams.vk_platform === "desktop_web") {
            bridge.send("VKWebAppResizeWindow", {
                "width": 700,
                "height": 930,
            }).then((data) => {
                // Обработка события в случае успеха
                //console.log('VKWebAppResizeWindow res', data);
            })
            .catch((error) => {
                // Обработка события в случае ошибки
                console.log('VKWebAppResizeWindow error', error);
            });
        }
    }
}
