export const shareThunk = () => {

    return (dispatch, getState) => {

        let state = getState();
        let messagePart = "узнала";

        if ( state.user && state.user.vkData && state.user.vkData.sex && parseInt(state.user.vkData.sex, 10) == 2 ) {
            messagePart = "узнал";
        }
        
        bridge.send(
            "VKWebAppShowWallPostBox",
            {
                //'message': "Well, Hello Mr. Fancy Pants! \nhttps://vk.com/app5898182_-169567248#s=889421&utm_term=10213847",
                ////'attachments': "https://vk.com/app5898182_-196584509#s=880773",
                ////'attachments': "video-11283947_456239621",
                ////'attachments': "photo-193196974_457239023",
                //'attachments': "photo-193196974_457239023,https://vk.com/app5898182_-196584509#s=880773",
    
                'message': `Я ${messagePart} свой Код Жизни! Хочешь узнать свой?\n 👉 https://vk.com/app7839536`,
                'attachments': "https://vk.com/app7839536",
    
                'guid': Date.now(),
            }
        ).then((data) => {
            // Обработка события в случае успеха
            console.log('VKWebAppShowWallPostBox res', data);
        })
        .catch((error) => {
            // Обработка события в случае ошибки
            console.log('VKWebAppShowWallPostBox error', error);
        });
        
    }
}
